<template>
<div class="cartcontrol">
 <transition name="move">
 	<div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart">
 		<span class="inner icon-remove_circle_outline"></span>
 	</div>
 </transition>	
 	<div class="cart-count" v-show="food.count>0">{{food.count}}</div>
 	<div ref="addCart" class="cart-add icon-add_circle" @click.stop.prevent="addCart"></div>
</div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue';
export default{
	props:{
		food:{
			type:Object
		},
    selectFoods:{
      type:Array,
      default(){
        return [];
      }
    }
	},
	methods:{
		addCart(){
			if(!this.food.count){
				Vue.set(this.food, 'count' , 1);
			}else{
				this.food.count++;
			}
		},
		decreaseCart(){
			if(this.food.count){
				this.food.count--;
			}

      if(this.selectFoods.length == 1 && this.food.count == 0){
        this.$store.commit('getCartAddDom',false);
      }
		}
	}
  }

</script>

<style lang="stylus" rel="stylesheet/stylus">
  .cartcontrol
    font-size: 0
    .cart-decrease
      display: inline-block
      padding: 0 6px 0 6px
      line-height: 24px
      font-size: 24px
      color: rgb(0, 160, 220)
      transition: all 0.4s linear
      &.move-enter-to,&.move-leave     
        opacity: 1
        transform: translate3d(0, 0, 0)
        .inner
          transition: all 0.4s linear
          display: inline-block    
          transform: rotate(0)
      &.move-enter, &.move-leave-to
        opacity: 0
        transform: translate3d(24px, 0, 0)
        .inner  
          transition: all 0.4s linear        
          display: inline-block    
          transform: rotate(180deg)
    .cart-count
      display: inline-block
      vertical-align: top
      width: 12px
      padding-top: 6px
      line-height: 24px
      text-align: center
      font-size: 10px
      color: rgb(147, 153, 159)
    .cart-add
      display: inline-block
      padding: 6px
      line-height: 24px
      font-size: 24px
      color: rgb(0, 160, 220)
</style>


